html {
  font-size: 62.5%;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.Board {
  display: grid;
  grid-auto-columns: 1fr;
  grid-column-gap: 2.5rem;
  background-color: #ECEEEE;
  height: 100vh;
  padding: 6rem 2.5rem; }

.Card {
  background-color: #FFF;
  color: #393C42;
  margin-bottom: 0.4rem; }
  .Card__contents {
    padding: 1.4rem;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .Card__arrow {
    font-weight: 700;
    cursor: pointer; }
    .Card__arrow:hover {
      color: #747a86; }
  .Card__text {
    margin: 0 auto;
    padding: 0 1.2rem;
    text-align: center; }

.Column {
  grid-row: 1; }
  .Column__title {
    height: 4rem;
    text-align: center;
    display: flex;
    margin-bottom: 1.2rem; }
    .Column__title--text {
      margin: auto; }
    .Column__title--lightpurple {
      background-color: #AEADF0; }
    .Column__title--lightpink {
      background-color: #C38D94; }
    .Column__title--darkpink {
      background-color: #A76571; }
    .Column__title--darkpurple {
      background-color: #565676; }
  .Column__add {
    height: 4.6rem;
    padding: 0.4rem 1.4rem; }
    .Column__add button {
      border: none;
      outline: none;
      background-color: transparent;
      font-size: inherit;
      font-weight: 700;
      color: #787F8C;
      cursor: pointer; }
      .Column__add button:hover {
        color: #9499a3; }
